.style {
  width: 35%;
  margin: 0 auto;
  background-color: rgb(36, 74, 92);
  margin-top: 10%;
  line-height: 50px;
  height: 250px;
}
.article-block {
  background-color: #ddd;
  width: 20%;
  height: 200px;
  background-color: rgb(36, 74, 92);
  margin: 2% auto;
}
.title {
  text-align: center;
  background-color: #eee;
  width: 100%;
  height: 35px;
  line-height: 35px;
}
.editButton {
  background-color: rgb(141, 24, 78);
  width: 35%;
  color: #fff;
  height: 45px;
  border: none;
  margin: 45px 50px 0 23px;
}
.removeButton {
  background-color: rgb(150, 26, 18);
  width: 35%;
  color: #fff;
  height: 45px;
  border: none;
}
.saveButton {
  background-color: rgb(25, 129, 25);
  width: 35%;
  color: #fff;
  height: 45px;
  border: none;
  margin: 45px 50px 0 23px;
}
textarea {
  resize: none;
}


